.delimeter {
    width: 100%;
    display: flex;
    align-items: center;
    // margin: 25px 0;

    .delimeter-line {
        background-color: #DADEE9;
        flex: 1;
        height: 1px;
    }

    .delimeter-circle {
        width: 24px;
        height: 24px;
        color: #DADEE9;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 16px;
    }
}